import React, { FC, useEffect, useState } from 'react'
import clsx from 'clsx'
import { ArrowDownIcon } from '../../icons/ArrowDown'
import { Typography } from '../Typography'
import { DropdownNextProps } from './types'
import './DropdownNext.scss'

const DropdownNext: FC<DropdownNextProps> = ({
  arrowIcon = null,
  children,
  isDisabled = false,
  className,
  title = '',
  icon = null,
  isOpenByDefault = false,
  renderTitle,
}) => {
  const [isOpen, setIsOpen] = useState(isOpenByDefault)

  useEffect(() => {
    if (isDisabled) setIsOpen(false)
  }, [isDisabled])

  return (
    <div
      className={clsx(
        'DropdownNext',
        className,
        isOpen && 'DropdownNext_Open',
        isDisabled && 'DropdownNext_Disabled',
      )}
    >
      <button
        className={clsx('DropdownNext__TitleWrapper', 'reset-button-styles')}
        onClick={(): void => setIsOpen(!isOpen)}
      >
        {renderTitle ? (
          renderTitle()
        ) : (
          <div className={clsx('DropdownNext__IconWrapper')}>
            {icon}
            <Typography
              className='DropdownNext__Title'
              text={title}
              type='Body1B'
            />
          </div>
        )}
        <div className='DropdownNext__ArrowIcon'>
          {arrowIcon || <ArrowDownIcon />}
        </div>
      </button>
      {isOpen && children}
    </div>
  )
}

export default DropdownNext
